:root {
  --ftm-red: #dd3230;
}

svg {
  width: 100%;
}

button {
  text-align: center;
  color: white;
  background-color: var(--ftm-red);
  border: none;
  border-radius: 0.2em;
  padding: 1em 2em;
  margin: 1em;
  cursor: pointer;
  width: 8em;
}

body {
  margin: 0;
  font-family: 'PT Sans', sans-serif;
  font-size: small;
}
